import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import AssetServiceTypeUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetServiceType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/asset-service-types";


class UpdateAssetServiceType extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editAssetServiceType } = this.props;
    const { id } = this.props.match.params;
    editAssetServiceType(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetServiceType.error) {
      notification("error", this.props.assetServiceType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetServiceType.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { updateAssetServiceType } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing location code
    //  value.location_code = value.location_code.toUpperCase();
    updateAssetServiceType(id, value);
  };


  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="assetServiceTypes.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.assetServiceType.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <AssetServiceTypeUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                assetServiceType={this.props.assetServiceType}
              //handleChangeAssetFiscal={this.handleChangeAssetFiscal}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    assetServiceType: state.assetServiceType
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateAssetServiceType);
