import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Row, Col, Alert, InputNumber } from "antd";
import actions from "../../../../redux/asset/assetServiceType/actions";

const { Option } = Select;
// const { TextArea } = Input;

class AssetServiceTypeUpdateForm extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };


  render() {
    //console.log(this.state.asset_fiscal);
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { assetServiceType } = this.props;
    const { loading, asset_categories } = assetServiceType;
    const data = assetServiceType.asset_service_type ? assetServiceType.asset_service_type : [];
    // var phone = data.phone + "";
    // phone = phone.substring(2);
    const used = data.is_used;
    const error = assetServiceType.errorField ? assetServiceType.errorField : [];
    // console.log(data);
    // const loop = array =>
    //   array.map(data => {
    //     let perData = {
    //         title : data.asset_category_code + " (" + data.asset_category_name + ")",
    //         value : data.id_asset_category,
    //         key : data.id_asset_category,
    //         children : data.children ? loop(data.children) : null
    //       }

    //     return perData
    //   })

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="assetServiceType.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="assetServiceTypes.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="assetCategories.pageHeader" />}
            >
              {getFieldDecorator("id_asset_category", {
                initialValue: data.id_asset_category,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={loading}
                  showSearch
                  optionFilterProp="children"
                  style={{ width:"100%" }}
                >
                  {asset_categories.map((data, key) => (
                    <Option key={key} value={data.id_asset_category}>
                      { data.asset_category_code + " (" + data.asset_category_name + ")"}
                    </Option>
                  ))}
                </Select>
              )}
              {/* {getFieldDecorator("id_asset_category", {
                initialValue: data.id_asset_category,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <TreeSelect
                  placeholder={
                    <IntlMessages id="assetServiceTypes.select.assetCategory" />
                  }
                  style={{ width: "95%" }}
                  allowClear={true}
                  showSearch={true}
                  treeNodeFilterProp="title"
                  dropdownStyle={{ maxHeight: 300, overflow: 'auto' }}
                  treeData={loop(asset_categories)}
                  treeDefaultExpandAll
                  loading={this.props.assetServiceType.loading}
                  disabled={used ? true : false}
                />
              )} */}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="assetServiceTypes.add.code" />}
              validateStatus={
                error.asset_service_type_code || getFieldError("asset_service_type_code")
                  ? "error"
                  : null
              }
              help={
                error.asset_service_type_code
                  ? error.asset_service_type_code
                  : getFieldError("asset_service_type_code")
              }
            >
              {getFieldDecorator("asset_service_type_code", {
                initialValue: data.asset_service_type_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="assetServiceTypes.add.name" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input 
                disabled={used ? true : false}

              
              />)}
            </Form.Item>
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="assetServiceTypes.intervalBy" />}
            >
              {getFieldDecorator("interval_by", {
                initialValue: data.interval_by,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                ]
              })(
                <Radio.Group
                  // disabled={used ? true : false}
                  style={{ display: "flex" }}
                  buttonStyle="solid"
                  //onChange= {this.handleChangeFromDepreciationMethod}
                  loading={loading}
                >
                  <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="time" /></Radio.Button>
                  <Radio.Button style={{ flex: "1", textAlign: "center" }} value={2}><IntlMessages id="parameter" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

            { getFieldValue('interval_by') === 1
              ? 
                (
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="assetServiceTypes.intervalUnit" />}
                  >
                    {getFieldDecorator("interval_unit", {
                      initialValue: data.interval_unit,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                      ]
                    })(
                      <Radio.Group
                        // disabled={used ? true : false}
                        style={{ display: "flex" }}
                        buttonStyle="solid"
                        //onChange= {this.handleChangeFromDepreciationMethod}
                        // loading={loading}
                      >
                        <Radio.Button style={{ flex: "1", textAlign: "center" }} value="days"><IntlMessages id="days" /></Radio.Button>
                        <Radio.Button style={{ flex: "1", textAlign: "center" }} value="months"><IntlMessages id="months" /></Radio.Button>
                        <Radio.Button style={{ flex: "1", textAlign: "center" }} value="years"><IntlMessages id="years" /></Radio.Button>
                      </Radio.Group>
                    )}
                  </Form.Item>
                )
              : 
                (
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="assetServiceTypes.intervalUnit" />}
                  >
                    {getFieldDecorator("interval_unit", {
                      initialValue: data.interval_unit,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                        {
                          max: 15,
                          message: <IntlMessages id="maxLength15" />
                        }
                      ]
                    })(
                      <Input  />
                    )}
                  </Form.Item>
                )

            }

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="assetServiceTypes.intervalValue" />}
            >
              {getFieldDecorator("interval_value", {
                initialValue: data.interval_value,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <InputNumber
                  formatter={value =>
                    `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  //min={0}
                  //disabled={used ? true : false}
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>

          </div>
        </div>
  

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
            disabled={used ? true : false}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedAssetServiceTypeUpdateForm = Form.create({ name: "update_asset_service_type" })(AssetServiceTypeUpdateForm);

const mapStateToProps = state => {
  return { assetServiceType: state.assetServiceType };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedAssetServiceTypeUpdateForm);
