const actions = {
  getAssetServiceTypes: (page, search, filter) => ({
    type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getAssetServiceFiscal: id_asset_service_fiscal => ({
    type: 'GET_ASSET_SERVICE_FISCAL_REQUEST',
    payload: { id_asset_service_fiscal: id_asset_service_fiscal }
  }),
  createAssetServiceType: () => ({
    type: 'CREATE_ASSET_SERVICE_TYPE_REQUEST'
  }),
  storeAssetServiceType: data => ({
    type: 'STORE_ASSET_SERVICE_TYPE_REQUEST',
    payload: { body: data }
  }),
  editAssetServiceType: id_asset_service_type => ({
    type: 'EDIT_ASSET_SERVICE_TYPE_REQUEST',
    payload: { id_asset_service_type: id_asset_service_type }
  }),
  updateAssetServiceType: (id_asset_service_type, data) => ({
    type: 'UPDATE_ASSET_SERVICE_TYPE_REQUEST',
    payload: {
      id_asset_service_type: id_asset_service_type,
      body: data
    }
  }),
  showAssetServiceType: id_asset_service_type => ({
    type: 'SHOW_ASSET_SERVICE_TYPE_REQUEST',
    payload: { id_asset_service_type: id_asset_service_type }
  }),
  deactivateAssetServiceType: id_asset_service_type => ({
    type: 'DEACTIVATE_ASSET_SERVICE_TYPE_REQUEST',
    payload: { id_asset_service_type }
  }),
  activateAssetServiceType: id_asset_service_type => ({
    type: 'ACTIVATE_ASSET_SERVICE_TYPE_REQUEST',
    payload: { id_asset_service_type }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
